import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { TabsPage } from './tabs.page';

const routes: Routes = [
  {
    path: '',
    component: TabsPage,
    children: [
      {
        path: 'check-beacon',
        loadChildren: () => import('../check-beacon/check-beacon.module').then( m => m.CheckBeaconPageModule)
      },
      {
        path: 'prize-list',
        loadChildren: () => import('../prize-list/prize-list.module').then( m => m.PrizeListPageModule)
      },
      {
        path: 'achievement-list',
        loadChildren: () => import('../achievement-list/achievement-list.module').then( m => m.AchievementListPageModule)
      },
      {
        path: 'game-list',
        loadChildren: () => import('../game-list/game-list.module').then( m => m.GameListPageModule)
      },
      {
        path: 'profile',
        loadChildren: () => import('../profile/profile.module').then( m => m.ProfilePageModule)
      },
      {
        path: '',
        redirectTo: '/tabs/check-beacon',
        pathMatch: 'full'
      }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
})
export class TabsPageRoutingModule {}
